  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.llms-spinning {
  background: rgba(250, 250, 250, 0.7);
  bottom: 0;
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  top: 0; }

.llms-spinner {
  -webkit-animation: llms-spinning 1.5s linear infinite;
          animation: llms-spinning 1.5s linear infinite;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 4px solid #313131;
  border-radius: 50%;
  height: 40px;
  left: 50%;
  margin-left: -20px;
  margin-top: -20px;
  position: absolute;
  top: 50%;
  width: 40px; }
  .llms-spinner.small {
    border-width: 2px;
    height: 20px;
    margin-left: -10px;
    margin-top: -10px;
    width: 20px; }

@-webkit-keyframes llms-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  50% {
    border-radius: 5%; }
  100% {
    -webkit-transform: rotate(220deg);
            transform: rotate(220deg); } }

@keyframes llms-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  50% {
    border-radius: 5%; }
  100% {
    -webkit-transform: rotate(220deg);
            transform: rotate(220deg); } }

